export enum ClaimTypesEnum {
  application = "application",
  document = "document",
  exam = "exam",
  marks_publication = "marks publication",
  placement = "placement",
}

export const getDocumentById = (
  candidateDocuments: DocumentClaimUser[],
  document_id: string
): DocumentClaimUser | null => {
  let result: DocumentClaimUser | undefined = candidateDocuments.find(
    (item) => item.doc_id === document_id
  );
  return result ? result : null;
};

export const whichClaimType = (
  thisClaim: CandidateClaimInterface
): ClaimTypesEnum | undefined => {
  if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
    return ClaimTypesEnum.application;
  }
  if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
    return ClaimTypesEnum.document;
  }
  if (thisClaim.type_key === PlansTypeKeys.EXAMINATION) {
    return ClaimTypesEnum.exam;
  }
  if (thisClaim.type_key === PlansTypeKeys.MARKS_PUBLICATION) {
    return ClaimTypesEnum.marks_publication;
  }
  if (thisClaim.type_key === PlansTypeKeys.PLACEMENT_TRANSFER) {
    return ClaimTypesEnum.placement;
  }
};

export const whichClaimStatus = (
  thisClaim: CandidateClaimInterface
): {
  classes: string;
  status: string;
} => {
  let classes: string, status: string;
  switch (thisClaim.last_claim_status) {
    case ClaimStatus.COMPLETED:
      classes = "text-blue-700 italic font-semiblod";
      status = capitalizeFirstLetter(ClaimStatus.COMPLETED.toLocaleLowerCase());
      break;
    case ClaimStatus.EVALUATED:
      classes = "text-green-700 italic font-semiblod";
      status = capitalizeFirstLetter(ClaimStatus.EVALUATED.toLocaleLowerCase());
      break;

    case ClaimStatus.DELETED:
      classes = "text-red-700 italic font-semiblod";
      status = capitalizeFirstLetter(ClaimStatus.DELETED.toLocaleLowerCase());
      break;

    default:
      classes = "text-yellow-700 italic font-semiblod";
      status = capitalizeFirstLetter(ClaimStatus.PENDING.toLocaleLowerCase());
      break;
  }

  return { classes, status };
};

export const getClaimById = (
  claimList: CandidateClaimInterface[],
  claim_id: string
): CandidateClaimInterface | null => {
  let result: CandidateClaimInterface | undefined = claimList.find(
    (item) => item._id === claim_id
  );
  return result ? result : null;
};

export const getApplicationById = (
  candidateApplications: claimApplicationsInterface[],
  application_id: string
): claimApplicationsInterface | null => {
  let result: claimApplicationsInterface | undefined =
    candidateApplications.find(
      (item) => item.application_id === application_id
    );
  return result ? result : null;
};

export const displayDocClaimTitle = (
  documentCategories: DocumentReducerInterface,
  candidateDocuments: DocumentClaimUser[],
  thisClaim: CandidateClaimInterface
): string => {
  let result: string = "";

  // CHECK IF CLAIM IS FOR DOCUMENTS ===============================================
  if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
    let thisDoc = getDocumentById(candidateDocuments, thisClaim.claiming_on_id);
    result = thisDoc
      ? ClaimGetDocTitle(documentCategories, thisDoc).main
      : "Document not found";
  }
  // END CHECK IF CLAIM IS FOR DOCUMENTS ===============================================
  return result;
};

export const displayClaimTitle = (
  claiming: ClaimingReducerType,
  documentReducer: DocumentReducerInterface,
  allCourse: course[] | null,
  thisClaim: CandidateClaimInterface
): string => {
  let result: string = thisClaim.claiming_on_id;

  if (
    thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION &&
    claiming.candidateDocuments
  ) {
    result = displayDocClaimTitle(
      documentReducer,
      claiming.candidateDocuments,
      thisClaim
    );
  }
  if (
    thisClaim.type_key === PlansTypeKeys.APPLICATION &&
    claiming.candidateApplications
  ) {
    let thisApplication = getApplicationById(
      claiming.candidateApplications,
      thisClaim.claiming_on_id
    );
    if (thisApplication) {
      result = thisApplication.position.position_title;
    } else {
      result = "Application not found";
    }
  }

  if (
    thisClaim.type_key === PlansTypeKeys.EXAMINATION &&
    allCourse &&
    claiming.myAnswer
  ) {
    let thisAnswer = getCourseIdBySheetId(
      claiming.myAnswer,
      thisClaim.claiming_on_id
    );
    if (thisAnswer && getCourseById(allCourse, thisAnswer.course)) {
      result = getCourseById(allCourse, thisAnswer.course)!.course_name;
    } else {
      result = "Exam not found";
    }
  }

  if (thisClaim.type_key === PlansTypeKeys.PLACEMENT_TRANSFER) {
    result = claiming.myPlacement
      ? "Placed on " + claiming.myPlacement.placed.school.school_name
      : "Placement not found";
  }

  return result;
};

