type AppProps = {
  auth: Auth;
  claiming: ClaimingReducerType;
  application_sessions: SessionPlanInterface[] | null;
  courses: course[] | null;
  documentReducer: DocumentReducerInterface;
  getMyPlacement: Function;
  getMyClaims: Function;
  loadCourses: Function;
  getMyAnswers: Function;
  GetAllSessionPlans: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

type AppState = {
  loading: boolean;
  loadingSession: boolean;
  thisPeriod: SessionPlanInterface | null;
  periodChanged: boolean;
  viewClaim: boolean;
  claimId: string | null;
  claimForm: boolean;

  // FOR FORM ================
  viewImage: boolean;
  loadingRequest: boolean;
  reClaiming: boolean;
  reClaimingMsg: string;
  formError: {
    element: string;
    msg: string;
  } | null;
};

class Index extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadingSession: true,
      thisPeriod: null,
      periodChanged: false,
      viewClaim: false,
      claimId: null,
      claimForm: false,

      // FOR FORM ================
      viewImage: false,
      loadingRequest: false,
      reClaiming: false,
      reClaimingMsg: "",
      formError: null,
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingSession = (value: boolean) => {
    this.setState({ loadingSession: value });
  };

  setViewClaim = (value: boolean) => {
    this.setState({ viewClaim: value });
  };

  setPeriodChanged = (value: boolean) => {
    this.setState({ periodChanged: value });
  };

  setClaimForm = (value: boolean) => {
    this.setState({ claimForm: value });
  };

  setViewImage = (value: boolean) => {
    this.setState({ viewImage: value });
  };

  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };

  setReClaiming = (value: boolean) => {
    this.setState({ reClaiming: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  setReClaimingMsg = (value: string) => {
    this.setState({ reClaimingMsg: value });
  };

  setClaimId = (value: string | null) => {
    this.setState({ claimId: value });
  };

  setThisPeriod = (value: SessionPlanInterface | null) => {
    this.setState({ thisPeriod: value });
  };

  loadClaims = (thisPeriod: SessionPlanInterface) => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (this.props.claiming.myClaims === null && thisPeriod !== null) {
      this.props.getMyClaims(
        this.props.auth.user.user_id,
        thisPeriod.session_plan_id,
        (status: boolean, msg: string) => {
          !status && this.setLoading(status);
          if (status === false && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (thisPeriod && this.state.loading) {
      this.setLoading(false);
    }
  };

  setSession = (sessions: SessionPlanInterface[]) => {
    // SETTING ACTIVE SESSION ==========================
    if (this.state.thisPeriod === null && sessions.length > 0) {
      let thisSession = sessions.find((item) => item.is_active === true);
      this.setThisPeriod(thisSession ? thisSession : sessions[0]);
      this.loadClaims(thisSession ? thisSession : sessions[0]);
      this.loadExamAnswers(
        thisSession ? thisSession : sessions[0],
        this.props.auth.user.user_id
      );
    }
  };

  loadExamAnswers = (
    activeSessionPlan: SessionPlanInterface,
    user_id: string
  ) => {
    this.props.getMyAnswers(
      activeSessionPlan.session_plan_id,
      user_id,
      (status: boolean, msg: string, data: SessionPlanInterface | "none") => {
        this.setLoading(status);
        if (!status && msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
        if (data) {
          console.log("====================================");
          console.log({ loadExamAnswers: data });
          console.log("====================================");
        }
      }
    );
  };

  componentDidMount = () => {
    if (
      this.props.courses === null ||
      (this.props.courses.length === 0 && this.state.loading)
    ) {
      this.props.loadCourses(this.setLoading);
      this.setLoading(false);
    } else {
      this.setLoading(false);
    }

    // LOADING APPLICATION SESSION ====================================
    if (this.props.application_sessions === null) {
      this.props.GetAllSessionPlans(
        (status: boolean, msg: string, data?: SessionPlanInterface[]) => {
          if (!status) {
            this.setLoadingSession(false);
            if (msg.length) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            if (data && data.length > 0) {
              // SETTING ACTIVE SESSION ==========================
              if (this.state.thisPeriod === null) {
                this.setSession(data);
              }
            }
          }
        }
      );
    } else {
      this.setSession(this.props.application_sessions);
      this.setLoadingSession(false);
    }

    if (!this.props.documentReducer.documentCategory) {
      this.props.getDocumentCategory();
      this.setLoading(false);
    } else if (this.state.loading) {
      this.setLoading(false);
    }
    if (!this.props.documentReducer.documentSubCategory && this.state.loading) {
      this.props.getDocumentSubCategory();
      this.setLoading(false);
    } else if (this.state.loading) {
      this.setLoading(false);
    }

    if (!this.props.documentReducer.documentFields && this.state.loading) {
      this.props.getDocumentFields();
      this.setLoading(false);
    } else if (this.state.loading) {
      this.setLoading(false);
    }

    if (this.props.claiming.myPlacement === null) {
      this.props.getMyPlacement(
        this.props.auth.user.user_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status) {
            if (msg.length) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
          }
        }
      );
    }
  };

  isDeleteAllowed = (thisCLaim: CandidateClaimInterface): boolean => {
    if (!isClaimEvaluated(thisCLaim, 1)) {
      return true;
    }
    return false;
  };

  onPeriodChange = (period_id: string) => {
    this.setPeriodChanged(true);
    this.props.getMyClaims(
      this.props.auth.user.user_id,
      period_id,
      (status: boolean, msg: string) => {
        this.setLoadingRequest(status);
        !status && this.setPeriodChanged(false);
        if (status === false && msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      }
    );
  };

  render() {
    if (this.props.application_sessions === null) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    if (this.props.application_sessions.length === 0) {
      return (
        <div className="bg-yellow-100 rounded flex flex-col items-center justify-center p-6 my-8 text-yellow-900 border border-yellow-900">
          <MdInfoOutline className="text-6xl md:text-9xl" />
          <div className="text-center flex flex-col">
            <span className="text-2xl md:text-4xl font-extrabold">
              No active session
            </span>
            <span className="text-lg">
              Please Wait for the system to publish the sessions or contact the
              administrator
            </span>
          </div>
        </div>
      );
    }

    if (this.props.claiming.myClaims === null) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    return (
      <div>
        {/* SIDE NAVIGATION */}
        {!this.state.viewClaim && (
          <SideNavigation>
            <button
              className="btn mb-3 border-2 border-blue-500 hover:bg-blue-500 hover:text-white flex items-center flex-1 font-bold w-full focus:outline-none"
              onClick={() => this.setClaimForm(true)}
              disabled={
                this.state.viewClaim ||
                this.state.loading ||
                this.state.loadingRequest
              }
            >
              <MdAdd className="inline-block mr-1 text-xl font-bold" />
              <span>Claiming</span>
            </button>

            {/* FILTER SECTION */}
            <FilterByType loadingClaims={this.state.loadingRequest} />
            {/* END FILTER SECTION */}
          </SideNavigation>
        )}
        {/* END SIDE NAVIGATION */}

        {/* ------------------------------------------------------------------- */}

        {/* CLAIM TITLE ===================================== */}
        {!this.state.viewClaim && (
          <ClaimHeaderTitle
            headerTitle={`${
              this.props.claiming.selectedClaimType === "all"
                ? "All"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.DOCUMENT_VALIDATION
                ? "Documents"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.APPLICATION
                ? "Applications"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.EXAMINATION
                ? "Exam"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.MARKS_PUBLICATION
                ? "Marks publication"
                : this.props.claiming.selectedClaimType ===
                  PlansTypeKeys.PLACEMENT_TRANSFER
                ? "Placements"
                : ""
            } Claiming`}
            loading={this.state.loading}
            loadingSession={this.state.loadingSession}
            thisPeriod={this.state.thisPeriod}
            setThisPeriod={this.setThisPeriod}
            setLoadingSession={this.setLoadingSession}
            application_sessions={this.props.application_sessions}
            onPeriodChange={this.onPeriodChange}
            periodChanged={this.state.periodChanged}
          />
        )}
        {/* END CLAIM TITLE ===================================== */}

        {/* ----------------------------------------------------------------------- */}

        {!this.state.viewClaim && (
          <section className="bg-white w-full p-1">
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td className="px-2 py-2">Claim title</td>
                  {this.props.claiming.selectedClaimType === "all" && (
                    <td className="px-2 py-2">Claim type</td>
                  )}
                  <td className="px-2 py-2">Claim status</td>
                  <td className="px-2 py-2"></td>
                </tr>
              </thead>
              <tbody>
                {filterClaimsByClaimType(
                  this.props.claiming.myClaims,
                  this.props.claiming.selectedClaimType
                ).length <= 0 ? (
                  <tr
                    className="border-b border-gray-200 group hover:bg-gray-50 cursor-pointer"
                    key="key3201"
                  >
                    <td className="px-3 py-1" colSpan={4}>
                      <NoResult
                        theme={NoResultThemes.Light}
                        value={"No claim found!"}
                      />
                    </td>
                  </tr>
                ) : (
                  filterClaimsByClaimType(
                    this.props.claiming.myClaims,
                    this.props.claiming.selectedClaimType
                  ).map((claim, index) => (
                    <tr
                      className="border-b border-gray-200 group hover:bg-gray-50 cursor-pointer"
                      key={index + claim._id}
                      onClick={() => {
                        this.setViewClaim(true);
                        this.setClaimId(claim._id);
                      }}
                    >
                      <td className="px-3 py-1 whitespace-nowrap">
                        {index + 1}
                      </td>
                      <td className="px-3 py-1 whitespace-nowrap">
                        {displayClaimTitle(
                          this.props.claiming,
                          this.props.documentReducer,
                          this.props.courses,
                          claim
                        )}
                      </td>
                      {this.props.claiming.selectedClaimType === "all" && (
                        <td className="px-3 py-1 whitespace-nowrap">
                          {whichClaimType(claim)
                            ? capitalizeFirstLetter(whichClaimType(claim)!)
                            : "-"}
                        </td>
                      )}
                      <td className="px-3 py-1 whitespace-nowrap">
                        <span
                          className={`inline-block rounded-full duration-300 text-xs font-semibold px-3 py-1 ${
                            whichClaimStatus(claim).classes
                          }`}
                        >
                          {whichClaimStatus(claim).status}
                        </span>
                      </td>
                      <td className="px-3 py-1 text-center whitespace-nowrap">
                        <button className="btn bg-blue-500 text-white group-hover:bg-blue-600">
                          View
                        </button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </section>
        )}

        {/* ----------------------------------------------------------------------- */}

        {this.state.viewClaim &&
          this.props.claiming.myClaims &&
          this.state.claimId &&
          getClaimById(this.props.claiming.myClaims, this.state.claimId) && (
            <section className="bg-white w-full p-1 my-4">
              {/* VIEW CLAIM HEADER =============================== */}
              <section className="flex items-center gap-2 py-2 border-b">
                <button
                  className={`ml-3 bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                    this.state.loadingRequest
                      ? "cursor-not-allowed"
                      : "cursor-pointer"
                  } animate__animated animate__fadeIn`}
                  disabled={this.state.loadingRequest}
                  onClick={() => {
                    this.setViewClaim(false);
                    this.setViewImage(false);
                    this.setLoadingRequest(false);
                    this.setFormError(null);
                    this.setReClaiming(false);
                    this.setReClaimingMsg("");
                  }}
                >
                  <MdArrowBack className="w-7 h-7" />
                </button>
                {this.isDeleteAllowed(
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!
                ) && (
                  <button
                    className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                      this.state.loadingRequest
                        ? "cursor-not-allowed"
                        : "cursor-pointer"
                    } animate__animated animate__fadeIn hidden`}
                    disabled={this.state.loadingRequest}
                    // onClick={() => {
                    //   this.handleDelete();
                    // }}
                  >
                    <MdDelete className="w-7 h-7" />
                  </button>
                )}
              </section>
              {/* END VIEW CLAIM HEADER =============================== */}

              <section className="my-2">
                {/* VIEWING CLAIM OF A DOCUMENT ===================== */}
                {getClaimById(this.props.claiming.myClaims, this.state.claimId)!
                  .type_key === PlansTypeKeys.DOCUMENT_VALIDATION &&
                this.props.claiming.candidateDocuments &&
                getDocumentById(
                  this.props.claiming.candidateDocuments,
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!.claiming_on_id
                ) ? (
                  <ClaimDocView
                    thisDoc={
                      getDocumentById(
                        this.props.claiming.candidateDocuments,
                        getClaimById(
                          this.props.claiming.myClaims,
                          this.state.claimId
                        )!.claiming_on_id
                      )!
                    }
                    documentCategories={this.props.documentReducer}
                    thisClaim={
                      getClaimById(
                        this.props.claiming.myClaims,
                        this.state.claimId
                      )!
                    }
                    viewImage={this.state.viewImage}
                    setViewImage={this.setViewImage}
                    loadingRequest={this.state.loadingRequest}
                    setLoadingRequest={this.setLoadingRequest}
                    reClaiming={this.state.reClaiming}
                    setReClaiming={this.setReClaiming}
                    reClaimingMsg={this.state.reClaimingMsg}
                    setReClaimingMsg={this.setReClaimingMsg}
                    formError={this.state.formError}
                    setFormError={this.setFormError}
                  />
                ) : (
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!.type_key === PlansTypeKeys.DOCUMENT_VALIDATION && (
                    <NoResult
                      theme={NoResultThemes.Dark}
                      value={"Document not found"}
                    />
                  )
                )}
                {/* END VIEWING CLAIM OF A DOCUMENT ===================== */}

                {/* ############################################################### */}

                {/* VIEWING CLAIM OF AN APPLICATION ===================== */}
                {getClaimById(this.props.claiming.myClaims, this.state.claimId)!
                  .type_key === PlansTypeKeys.APPLICATION &&
                this.props.claiming.candidateApplications &&
                getApplicationById(
                  this.props.claiming.candidateApplications,
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!.claiming_on_id
                ) ? (
                  <ClaimApplView
                    thisApplication={
                      getApplicationById(
                        this.props.claiming.candidateApplications,
                        getClaimById(
                          this.props.claiming.myClaims,
                          this.state.claimId
                        )!.claiming_on_id
                      )!
                    }
                    documentCategories={this.props.documentReducer}
                    thisClaim={
                      getClaimById(
                        this.props.claiming.myClaims,
                        this.state.claimId
                      )!
                    }
                    viewPosition={this.state.viewImage}
                    setViewPosition={this.setViewImage}
                    loadingRequest={this.state.loadingRequest}
                    setLoadingRequest={this.setLoadingRequest}
                    reClaiming={this.state.reClaiming}
                    setReClaiming={this.setReClaiming}
                    reClaimingMsg={this.state.reClaimingMsg}
                    setReClaimingMsg={this.setReClaimingMsg}
                    formError={this.state.formError}
                    setFormError={this.setFormError}
                  />
                ) : (
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!.type_key === PlansTypeKeys.APPLICATION && (
                    <NoResult
                      theme={NoResultThemes.Dark}
                      value={"Application not found"}
                    />
                  )
                )}
                {/* END VIEWING CLAIM OF AN APPLICATION ===================== */}

                {/* VIEWING CLAIM OF AN EXAM ===================== */}
                {getClaimById(this.props.claiming.myClaims, this.state.claimId)!
                  .type_key === PlansTypeKeys.EXAMINATION &&
                this.props.claiming.myAnswer &&
                this.props.courses &&
                getCourseIdBySheetId(
                  this.props.claiming.myAnswer,
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!.claiming_on_id
                ) &&
                getCourseById(
                  this.props.courses,
                  getCourseIdBySheetId(
                    this.props.claiming.myAnswer,
                    getClaimById(
                      this.props.claiming.myClaims,
                      this.state.claimId
                    )!.claiming_on_id
                  )!.course
                ) ? (
                  <ClaimExamView
                    thisExam={
                      getCourseById(
                        this.props.courses,
                        getCourseIdBySheetId(
                          this.props.claiming.myAnswer,
                          getClaimById(
                            this.props.claiming.myClaims,
                            this.state.claimId
                          )!.claiming_on_id
                        )!.course
                      )!
                    }
                    thisClaim={
                      getClaimById(
                        this.props.claiming.myClaims,
                        this.state.claimId
                      )!
                    }
                    viewPosition={this.state.viewImage}
                    setViewPosition={this.setViewImage}
                    loadingRequest={this.state.loadingRequest}
                    setLoadingRequest={this.setLoadingRequest}
                    reClaiming={this.state.reClaiming}
                    setReClaiming={this.setReClaiming}
                    reClaimingMsg={this.state.reClaimingMsg}
                    setReClaimingMsg={this.setReClaimingMsg}
                    formError={this.state.formError}
                    setFormError={this.setFormError}
                    thisExamSheet={
                      getCourseIdBySheetId(
                        this.props.claiming.myAnswer,
                        getClaimById(
                          this.props.claiming.myClaims,
                          this.state.claimId
                        )!.claiming_on_id
                      )!
                    }
                  />
                ) : (
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!.type_key === PlansTypeKeys.EXAMINATION && (
                    <NoResult
                      theme={NoResultThemes.Dark}
                      value={"Exam not found"}
                    />
                  )
                )}
                {/* END VIEWING CLAIM OF AN EXAM ===================== */}

                {/* ----------------------------------------------------------- */}
                {/* VIEWING CLAIM OF A DOCUMENT ===================== */}
                {getClaimById(this.props.claiming.myClaims, this.state.claimId)!
                  .type_key === PlansTypeKeys.PLACEMENT_TRANSFER &&
                this.props.claiming.myPlacement ? (
                  <ClaimPlacementView
                    thisClaim={
                      getClaimById(
                        this.props.claiming.myClaims,
                        this.state.claimId
                      )!
                    }
                    loadingRequest={this.state.loadingRequest}
                    setLoadingRequest={this.setLoadingRequest}
                    reClaiming={this.state.reClaiming}
                    setReClaiming={this.setReClaiming}
                    reClaimingMsg={this.state.reClaimingMsg}
                    setReClaimingMsg={this.setReClaimingMsg}
                    formError={this.state.formError}
                    setFormError={this.setFormError}
                    myPlacement={this.props.claiming.myPlacement}
                  />
                ) : (
                  getClaimById(
                    this.props.claiming.myClaims,
                    this.state.claimId
                  )!.type_key === PlansTypeKeys.DOCUMENT_VALIDATION && (
                    <NoResult
                      theme={NoResultThemes.Dark}
                      value={"Document not found"}
                    />
                  )
                )}
                {/* END VIEWING CLAIM OF A DOCUMENT ===================== */}
              </section>
            </section>
          )}
        {/* end view claim */}

        {/* ----------------------------------------------------------------------- */}
        {/* CLAIM FORM  ################ */}
        {this.state.claimForm && (
          <ClaimForm
            formState={this.state.claimForm}
            setFormState={this.setClaimForm}
          />
        )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  sessionPlan,
  claiming,
  document,
  course,
}: StoreState): {
  auth: Auth;
  claiming: ClaimingReducerType;
  application_sessions: SessionPlanInterface[] | null;
  documentReducer: DocumentReducerInterface;
  courses: course[] | null;
} => {
  return {
    auth: auth,
    claiming,
    application_sessions: sessionPlan.allSessionPlans,
    documentReducer: document,
    courses: course.courses,
  };
};

export const CandidateClaim = connect(mapStateToProps, {
  getMyClaims,
  GetAllSessionPlans,
  loadCourses,
  getMyPlacement,
  getMyAnswers,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  PushPopupAlert,
})(Index);
