import axios from "axios";
import React from "react";
import { FaRegCheckCircle } from "react-icons/fa";
import { RiArrowGoBackFill } from "react-icons/ri";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../actions";
import { createReClaim } from "../../actions/claiming";
import {
  CandidateClaimInterface,
  ClaimDecisionStatus,
  ClaimStatus,
} from "../../interfaces";
import { candidatePlacementInterface } from "../../interfaces/placement";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { capitalizeFirstLetter, isEmptyOrSpaces } from "../../shared/dataTest";
import { API } from "../../utils/api";
import { setAxiosToken } from "../../utils/AxiosToken";
import { errorToText, showDate, timestampToDate } from "../../utils/functions";

const isReclaimAllowed = (thisClaim: CandidateClaimInterface): boolean => {
  if (thisClaim.claim_level === 1) {
    const _thisClaim = thisClaim.claims.find((item) => item.level === 1);
    if (_thisClaim) {
      if (_thisClaim.decision_taken_status === ClaimDecisionStatus.REJECTED) {
        return true;
      }
    }
  }
  return false;
};

export const isClaimEvaluated = (
  thisClaim: CandidateClaimInterface,
  level: number
): boolean => {
  const _thisClaim = thisClaim.claims.find(
    (item) =>
      item.level === level && item.claim_status === ClaimStatus.EVALUATED
  );
  if (_thisClaim) {
    return true;
  }
  return false;
};

type ClaimPlacementViewProps = {
  myPlacement: candidatePlacementInterface;
  thisClaim: CandidateClaimInterface;
  loadingRequest: boolean;
  setLoadingRequest: (value: boolean) => void;
  reClaiming: boolean;
  setReClaiming: (value: boolean) => void;
  reClaimingMsg: string;
  setReClaimingMsg: (value: string) => void;
  formError: {
    element: string;
    msg: string;
  } | null;
  setFormError: (
    value: {
      element: string;
      msg: string;
    } | null
  ) => void;

  // REDUX DATA =====================
  createReClaim: Function;
  PushPopupAlert: Function;
};

const ClaimPlacementView: React.FC<ClaimPlacementViewProps> = (props) => {
  const handleSubmit = async () => {
    if (isEmptyOrSpaces(props.reClaimingMsg)) {
      return props.setFormError({
        element: "comment",
        msg: "Claim comment is required!",
      });
    }
    if (!props.thisClaim) {
      return props.PushPopupAlert({
        title: "Claim sent successful",
        theme: PopupAlertTheme.warning,
      });
    }

    props.setLoadingRequest(true);
    try {
      setAxiosToken();
      const res = await axios.put<CandidateClaimInterface>(
        `${API.CLAIMING}/user/reclaim/${props.thisClaim._id}`,
        {
          reason: props.reClaimingMsg,
        }
      );
      console.log({ res: res.data });
      if (res.status === 201 || res.status === 200) {
        props.createReClaim(res.data);
        props.PushPopupAlert({
          title: "Claim sent successful",
          theme: PopupAlertTheme.success,
        });
        props.setReClaimingMsg("");
        props.setReClaiming(false);
      }
    } catch (error) {
      console.error({ error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    props.setLoadingRequest(false);
  };

  return (
    <section className="">
      <div className="w-full bg-white px-4 py-1 divide-y-2 space-y-2">
        {/* DOCUMENT DETAILS ==================================================== */}
        <section className="flex flex-col">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2 text-sm text-gray-600">
              <span className={`italic`}>School:</span>
              <span className="text-gray-800 font-bold text-xl">
                {props.myPlacement.placed.school.school_name}
              </span>
            </div>
          </div>
          {/* <div className="flex items-center">
            <div className="flex items-center gap-2 text-sm text-gray-600">
              <span className={`italic`}>Position:</span>
              <span className="text-gray-800 font-bold text-xl">
                {props.myPlacement.placed.school.school_name}
              </span>
            </div>
          </div> */}
          <div className="flex items-center">
            <div className="flex items-center gap-2 text-sm text-gray-600">
              <span className={`italic`}>District:</span>
              <span className="text-gray-800 font-bold text-xl">
                {props.myPlacement.placed.school.district_id.district_name}
              </span>
            </div>
          </div>
        </section>
        {/* END DOCUMENT DETAILS ==================================================== */}

        {/* ################################################################################ */}

        {/* CLAIM LEVELS DETAILS ==================================================== */}
        {props.thisClaim.claims.length > 0 &&
          props.thisClaim.claims.map((claim, index) => (
            <section className="flex flex-col px-2 pt-3" key={index + claim.id}>
              {/* CLAIM COMMENT ==>> LEVEL 1 ==================================================== */}
              <div className="flex flex-col items-start gap-2">
                <p>{claim.reason}</p>
                <span className="text-gray-500 text-xs">
                  {showDate(claim.createdAt) +
                    " at " +
                    timestampToDate(claim.date_created).timeFR}
                </span>
              </div>

              {/* RESPONSE OF LEVELS ======================================= */}
              {isClaimEvaluated(props.thisClaim, claim.level) && (
                <section className="mt-4">
                  <span className="text-gray-500 italic font-semibold">
                    Response (level {index + 1})
                  </span>
                  <div className="border-t-2">
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Status:</span>
                      <span
                        className={`font-bold ${
                          claim.decision_taken_status ===
                          ClaimDecisionStatus.REJECTED
                            ? "text-yellow-900"
                            : "text-blue-600"
                        } rounded px-3 py-1`}
                      >
                        {capitalizeFirstLetter(
                          claim.decision_taken_status.toLocaleLowerCase()
                        )}
                      </span>
                    </div>
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Evaluated by:</span>
                      <span className="font-semibold">
                        {claim.evaluater.name}
                      </span>
                    </div>
                    <div className="flex items-start gap-2 text-sm text-gray-600">
                      <span className={`italic`}>Reason:</span>
                      <span className="font-normal">
                        {claim.evaluation_comment}
                      </span>
                    </div>
                  </div>
                </section>
              )}
              {/* CLAIM COMMENT ==>> LEVELS ==================================================== */}
            </section>
          ))}
        {/* END CLAIM LEVELS DETAILS ==================================================== */}
      </div>
      <section>
        {!props.reClaiming && (
          <div className="mx-4 mb-5 mt-3 flex items-center justify-between">
            <span
              className={`${
                props.thisClaim.last_claim_status === ClaimStatus.PENDING
                  ? "text-yellow-700"
                  : props.thisClaim.last_claim_status === ClaimStatus.DELETED
                  ? "text-red-700"
                  : "text-green-700"
              } italic font-semibold`}
            >
              {capitalizeFirstLetter(
                props.thisClaim.last_claim_status.toLocaleLowerCase()
              )}
            </span>

            {isReclaimAllowed(props.thisClaim) && (
              <button
                className="btn btn-blue"
                onClick={() => props.setReClaiming(true)}
                disabled={props.loadingRequest}
              >
                Reclaim
              </button>
            )}
          </div>
        )}
        {/* right part */}
        {props.reClaiming && (
          <div className="flex flex-col md:flex-row items-end md:items-start gap-1 w-full">
            <div className="w-full px-2">
              <textarea
                placeholder={`Reason of your reclaim...`}
                cols={50}
                rows={3}
                className={`border border-${
                  props.formError && props.formError.element === "comment"
                    ? "red"
                    : "gray"
                }-400 rounded px-3 py-1 w-full focus:outline-none`}
                style={{ resize: "none" }}
                value={props.reClaimingMsg}
                onChange={(e) => {
                  props.setReClaimingMsg(e.target.value);
                  props.setFormError(null);
                }}
                disabled={props.loadingRequest}
              ></textarea>
              {props.formError && props.formError.element === "comment" && (
                <p className="text-red-800">{props.formError.msg}</p>
              )}
            </div>
            <div className="flex flex-row md:flex-col gap-1 max-w-full px-2">
              <button
                className="bg-gray-700 hover:bg-gray-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                onClick={() => {
                  props.setReClaimingMsg("");
                  props.setReClaiming(false);
                  props.setFormError(null);
                }}
                disabled={props.loadingRequest}
              >
                <RiArrowGoBackFill className="w-5 h-5" />
                <span>Back</span>
              </button>
              <button
                className="bg-blue-700 hover:bg-blue-800 px-4 py-2 font-semibold rounded text-white inline-flex items-center space-x-2 focus:outline-none"
                disabled={props.loadingRequest}
                onClick={() => handleSubmit()}
              >
                {props.loadingRequest ? (
                  <div className="">Loading...</div>
                ) : (
                  <div className="inline-flex items-center space-x-2 gap-1">
                    <FaRegCheckCircle className="w-5 h-5" />
                    <span>Confirm</span>
                  </div>
                )}
              </button>
            </div>
          </div>
        )}
        {/* end right part */}
      </section>
    </section>
  );
};

export default connect(null, {
  createReClaim,
  PushPopupAlert,
})(ClaimPlacementView);
