import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../../actions";
import {
  GetClaimingTypes,
  getMyRejectedApplications,
  getMyRejectedDocuments,
  setSelectedClaimType,
} from "../../../actions/claiming";
import { PopupAlertData } from "../../../components/Alert/Popup";
import { MainItem } from "../../../components/Navigation/SideNavigation";
import { ClaimStatus, PlansTypeKeys } from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { ClaimingReducerType } from "../../../reducers/claiming";

type FilterByTypeProps = {
  // thisClaimType: ClaimTypesInterface | "ALL";
  // setThisClaimType: React.Dispatch<
  //   React.SetStateAction<ClaimTypesInterface | "ALL">
  // >;
  loadingClaims: boolean;
  FC_CountByType?: Function;

  // REDUX PROPS =======================================
  auth: Auth;
  claiming: ClaimingReducerType;
  GetClaimingTypes: Function;
  setSelectedClaimType: Function;
  getMyRejectedApplications: Function;
  getMyRejectedDocuments: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

const FilterByType: React.FC<FilterByTypeProps> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingRejectedApplications, setLoadingRejectedApplications] =
    useState<boolean>(true);
  const [loadingRejectedDocuments, setLoadingRejectedDocuments] =
    useState<boolean>(true);
  const [loadingClaimTypes, setLoadingClaimTypes] = useState<boolean>(true);

  useEffect(() => {
    if (props.claiming.candidateApplications === null) {
      props.getMyRejectedApplications(
        props.auth.user.user_id,
        (status: boolean, msg: string) => {
          if (status === false) {
            if (msg.length > 0) {
              props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            setLoadingRejectedApplications(false);
          }
        }
      );
    } else if (loadingRejectedApplications) {
      setLoadingRejectedApplications(false);
    }

    if (props.claiming.candidateDocuments === null) {
      console.log("filterrrrrrrrrrrrrrrrrrrr loadingRejectedDocuments");
      props.getMyRejectedDocuments((status: boolean, msg: string) => {
        if (status === false) {
          if (msg.length > 0) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          setLoadingRejectedDocuments(false);
        }
      });
    } else if (loadingRejectedDocuments) {
      setLoadingRejectedDocuments(false);
    }

    if (props.claiming.claimTypes === null) {
      props.GetClaimingTypes((status: boolean, msg: string) => {
        if (status === false) {
          if (msg.length > 0) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          setLoadingClaimTypes(false);
        }
      });
    } else if (loadingClaimTypes) {
      setLoadingClaimTypes(false);
    }

    if (
      !loadingRejectedApplications &&
      !loadingRejectedDocuments &&
      !loadingClaimTypes &&
      loading
    ) {
      setLoading(false);
    }
  }, [
    loading,
    loadingClaimTypes,
    loadingRejectedApplications,
    loadingRejectedDocuments,
    props,
  ]);

  const countClaims = (
    claiming: ClaimingReducerType,
    thisType: PlansTypeKeys | "all"
  ): number => {
    let result: number = 0;

    if (claiming.myClaims && thisType === "all") {
      result = claiming.myClaims.filter(
        (item) =>
          item.last_claim_status !== ClaimStatus.DELETED &&
          item.last_claim_status !== ClaimStatus.COMPLETED
      ).length;
    }
    if (claiming.myClaims && thisType !== "all") {
      result = claiming.myClaims.filter(
        (item) =>
          item.type_key === thisType &&
          item.last_claim_status !== ClaimStatus.DELETED &&
          item.last_claim_status !== ClaimStatus.COMPLETED
      ).length;
    }

    return result;
  };

  return (
    <React.Fragment>
      <span className="mt-2 text-sm">Filter by claim type</span>
      {/* WHILE LOADING DATA ================================== */}
      {(loading || props.loadingClaims) && (
        <section className="space-y-2">
          <div className="w-full h-7 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          <div className="w-full h-7 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          <div className="w-full h-7 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
          <div className="w-full h-7 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </section>
      )}
      {/* END WHILE LOADING DATA ================================== */}

      {/* AFTER LOADING DATA ================================== */}
      {!loading && !props.loadingClaims && (
        <section>
          {/* ALL ================================= */}
          <div
            className="mt-2"
            onClick={() => {
              // props.setThisClaimType("ALL");
              props.setSelectedClaimType("all");
            }}
          >
            <MainItem
              title="All"
              active={props.claiming.selectedClaimType === "all"}
              count={countClaims(props.claiming, "all")}
            />
          </div>
          {/* APPLICATION ================================= */}
          <div
            className="mt-2"
            onClick={() => {
              // props.setThisClaimType(ClaimTypesInterface.DOCUMENT);
              props.setSelectedClaimType(PlansTypeKeys.APPLICATION);
            }}
          >
            <MainItem
              title={"Application"}
              active={
                props.claiming.selectedClaimType !== "all" &&
                props.claiming.selectedClaimType === PlansTypeKeys.APPLICATION
              }
              count={countClaims(props.claiming, PlansTypeKeys.APPLICATION)}
            />
          </div>
          {/* DOCUMENT ================================= */}
          <div
            className="mt-2"
            onClick={() => {
              props.setSelectedClaimType(PlansTypeKeys.DOCUMENT_VALIDATION);
            }}
          >
            <MainItem
              title={"Document"}
              active={
                props.claiming.selectedClaimType !== "all" &&
                props.claiming.selectedClaimType ===
                  PlansTypeKeys.DOCUMENT_VALIDATION
              }
              count={countClaims(
                props.claiming,
                PlansTypeKeys.DOCUMENT_VALIDATION
              )}
            />
          </div>
          {/* EXAMINATION ================================= */}
          <div
            className="mt-2"
            onClick={() => {
              props.setSelectedClaimType(PlansTypeKeys.EXAMINATION);
            }}
          >
            <MainItem
              title={"Exam"}
              active={
                props.claiming.selectedClaimType !== "all" &&
                props.claiming.selectedClaimType === PlansTypeKeys.EXAMINATION
              }
              count={countClaims(props.claiming, PlansTypeKeys.EXAMINATION)}
            />
          </div>
          {/* PLACEMENT TRANSFER ================================= */}
          {false && (
            <div
              className="mt-2"
              onClick={() => {
                props.setSelectedClaimType(PlansTypeKeys.PLACEMENT_TRANSFER);
              }}
            >
              <MainItem
                title={"Placement"}
                active={
                  props.claiming.selectedClaimType !== "all" &&
                  props.claiming.selectedClaimType ===
                    PlansTypeKeys.PLACEMENT_TRANSFER
                }
                count={countClaims(
                  props.claiming,
                  PlansTypeKeys.PLACEMENT_TRANSFER
                )}
              />
            </div>
          )}
          {/* <div
            className="mt-2"
            onClick={() => {
              props.setThisClaimType(ClaimTypesInterface.APPLICATION);
            }}
          >
            <MainItem
              title={capitalizeFirstLetter(ClaimTypesInterface.APPLICATION)}
              active={props.thisClaimType === ClaimTypesInterface.APPLICATION}
              count={countClaims(props.claiming).applications}
            />
          </div>
          <div
            className="mt-2"
            onClick={() => {
              props.setThisClaimType(ClaimTypesInterface.PLACEMENT);
            }}
          >
            <MainItem
              title={capitalizeFirstLetter(ClaimTypesInterface.PLACEMENT)}
              active={props.thisClaimType === ClaimTypesInterface.PLACEMENT}
              count={countClaims(props.claiming).placement}
            />
          </div> */}
        </section>
      )}
      {/* END AFTER LOADING DATA ================================== */}
    </React.Fragment>
  );
};

const mapStateToProps = ({
  auth,
  claiming,
}: StoreState): { auth: Auth; claiming: ClaimingReducerType } => {
  return { auth, claiming };
};

export default connect(mapStateToProps, {
  GetClaimingTypes,
  setSelectedClaimType,
  getMyRejectedApplications,
  getMyRejectedDocuments,
  PushPopupAlert,
})(FilterByType);
