import React from "react";
import { AiFillDashboard } from "react-icons/ai";
import { SessionPlanInterface } from "../../interfaces";

type ClaimHeaderTitleProps = {
  headerTitle: string;
  loading: boolean;
  loadingSession: boolean;
  periodChanged: boolean;
  thisPeriod: SessionPlanInterface | null;
  setThisPeriod: (state: SessionPlanInterface | null) => void;
  setLoadingSession: (state: boolean) => void;
  application_sessions: SessionPlanInterface[];
  onPeriodChange: (period_id: string) => void;
};

const selectPeriod = (
  sessions: SessionPlanInterface[],
  period_id: string,
  setThisPeriod: (state: SessionPlanInterface | null) => void
) => {
  setThisPeriod(
    sessions.filter((item) => item.session_plan_id === period_id)[0]
  );
};

const ClaimHeaderTitle: React.FC<ClaimHeaderTitleProps> = (props) => {
  const isLoading = (): boolean => {
    if (props.loading || props.loadingSession || props.periodChanged) {
      return true;
    }
    return false;
  };

  return (
    <div className="mt-3 rounded flex flex-col md:flex-row md:justify-between items-start">
      <div className="flex flex-row items-center gap-3 mb-2">
        <AiFillDashboard className="text-6xl text-blue-600 hidden md:block" />
        <div className="flex flex-col">
          <span className="text-blue-600 text-3xl font-bold">
            {props.headerTitle}
          </span>
        </div>
      </div>

      {/* FILTER SECTION PERIOD && EDUCATION */}
      <section className="w-full md:w-auto my-2 md:my-0">
        <div>
          <select
            className={`bg-white text-gray-600 border border-gray-400 rounded p-2 focus:outline-none ${
              isLoading() ? "cursor-wait" : "cursor-default"
            }`}
            value={props.thisPeriod ? props.thisPeriod.session_plan_id : ""}
            disabled={isLoading()}
            onChange={(e) => {
              selectPeriod(
                props.application_sessions,
                e.target.value,
                props.setThisPeriod
              );
              props.setLoadingSession(true);
              props.onPeriodChange(e.target.value);
            }}
          >
            {props.application_sessions.map((session, item) => (
              <option
                key={session.session_plan_id + item}
                value={session.session_plan_id}
              >
                {session.session_plan_name}
              </option>
            ))}
          </select>
        </div>
      </section>
      {/* END FILTER SECTION PERIOD && EDUCATION */}
    </div>
  );
};

export default ClaimHeaderTitle;
